.sectionPics__main__container{
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap:7rem;
}

.sectionPics{
  display: flex;
  width: 100%;
  margin-bottom: 1rem;
  &__wrapper{
    padding: 1rem;
    height: 440px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__text__container {
    display: flex;
    flex-direction: column;
    height:75%;
    padding: 1rem;
  }
  &__text_header {
    height: 27%;
    width: 100%;
    text-align: center;
    animation: textShine 3s ease-in-out infinite alternate;
    -webkit-text-fill-color: transparent;
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    background-image: var(--text-gradient);
    background-size: 300% auto;
    font-family: 'Cuprum', sans-serif;
    font-size: 32px;
    @keyframes textShine {
      0% {
          background-position: 0% 50%;
      }
      100% {
          background-position: 100% 50%;
      }
    } 
  }
  &  p {
    font-size: 25px;
    text-align: center;
    height: 25%;
  }

  &__img__wrapper {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 35%;
  }

  &__img {
    height: 100%;
        width: auto;
  }
}
@media(max-width: 480px) {
  .sectionPics{
   display: flex;
   flex-direction: column;
    height: 100%;
    padding:10px;
  
    &__wrapper{
      height: 412px;
      width: 80%;
      gap:1rem;
      justify-content: center;
      width: 95%;
      padding: 0;
      margin-bottom: 1rem;
      
    }
    &__text__container {
      display: flex;
      flex-direction: column;
      height:65%;
      padding: 0;
      align-items: center;
    }
    & p {
      
      font-size: 20px;
      text-align: center;
      height: 100%;
      width: 78%;
    }
    &__text_header {
      font-size: 23px;
      
    
      text-align: center;
    }
    &__img {
      height: 150px;
          width: auto;
        } 
  }
}

@media(max-width: 926px) and (min-width: 750px) {
  .sectionPics{
  display: grid;
    grid-template-columns: repeat(2,1fr);
  height: 100%;
    padding:10px;
  
    &__wrapper{
      height: 412px;
      width: 80%;
      gap:1rem;
      
      width: 95%;
      padding: 0;
      margin-bottom: 2rem;
      
    }
    &__text__container {
      display: flex;
      flex-direction: column;
      height:65%;
      padding: 0;
      align-items: center;
    }
    & p {
      
      font-size: 20px;
      text-align: center;
      height: 100%;
      width: 78%;
    }
    &__text_header {
      font-size: 23px;
      
    
      text-align: center;
    }
    &__img {
      height: 150px;
          width: auto;
        } 
  }
}