.header {
  
  &__container {
  z-index:60;
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: relative;

  background-image: url('./black-pattern-background.jpg');
  background-attachment: fixed;
  background-size: 100%;
  background-repeat: no-repeat;
  height: 80px;
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  }
  &__navigation {
    display: flex;
    gap: 4rem;
    align-items: center;
  }    
  &__navigation__span {
    font-weight: 300;
    font-size: 19px;
    display: flex;
    align-items: center;
    gap:1rem;
    padding: 7px;
    cursor: pointer;
    &:hover {
      color: var(--main-text-color)
    }
  }
  &__navigation__socials {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  &__navigation__socials_item {
    height: 30px;
    width: 30px;
    color: var(--main-text-color)
  }
}
@media(max-width: 480px) {
  .header {
    &__container {
      height: 75px;
      max-width: 100%;
      padding-top: 1rem;
      padding-bottom: 1rem;
      padding-right: 0;
      padding-left: 0;
    }
    &__navigation__span {
      font-size: 14px; 
  }
} 
}